$color-background: #eee;
$color-background-hover: pink;
$color-border: #333;

$daysMap: (
  "mon": getDaysWidth(0, 100px, 1px),
  "tue": getDaysWidth(1, 100px, 1px),
  "wed": getDaysWidth(2, 100px, 1px),
  "thu": getDaysWidth(3, 100px, 1px),
  "fri": getDaysWidth(4, 100px, 1px),
  "sat": getDaysWidth(5, 100px, 1px),
  "sun": getDaysWidth(6, 100px, 1px),
);
